import recommendList from '@/components/school-list/recommend'
import videoList from '@/components/school-list/video'
import easyLoadimage from '@/components/easy-loadimage/easy-loadimage.vue'
import uniPopup from '@/components/uni-popup/uni-popup'
export default{
	props:{
		list:{
			type:Array,
			default:()=>[]
		},
		scrollTop:{
			type:Number,
			default:0
		}
	},
	components:{
		recommendList,
		videoList,
		easyLoadimage,
		uniPopup
	},
	data(){
		return {
			arr:[],
			bgShow:false,
			shareList:[
				{
					title:'微信'
				},
				{
					title:'朋友圈'
				}
			],
			floatPartList:[
				{
					icon:'icon-share',
					title:'分享'
				},
				{
					icon:'icon-baoming',
					title:'报名'
				},
				{
					icon:'icon-dislike',
					title:'不喜欢'
				}
			],
			operationList:[
				{
					icon:'icon-aixin',
					title:'收藏'
				},
				{
					icon:'icon-kefu',
					title:'咨询'
				},
				{
					icon:'icon-share',
					title:'分享'
				}
			]
		}
	},
	methods:{
		//点击浮动模块显示，且bgShow也显示
		floatPartClick(index){
		   this.$set(this.arr[index],'floatShow',true)
		   this.bgShow = true
		   // this.arr.map((item,i)=>{
			  //  if(index !== i){
				 //   this.$set(item,'floatShow',false)
			  //  }
		   // })
		},
		//进入详情
		goDetail(){
			uni.navigateTo({
				url:'/sub_school/pages/detail/index'
			})
		},
		//浮动模块中的操作按钮
		operationClick(index){
			this.sharePopup()
			console.log(index,'操作按钮')
		},
		//1、浮动模块显示时，只有一触摸到除浮动模块外的部分，bgShow 设置为false，floatShow 都为false
		//2、切换tababr时隐藏浮动模块
		touchstartPage(){
			this.bgShow = false
			this.arr.map((item,i)=>{
				   this.$set(item,'floatShow',false)
			})
		},
		// 收藏 客服 分享 按钮点击
		userOper(index){
			switch(index){
				case 0:
					this.chatBtn()
				break;
				case 1:
					this.collectBtn()
				break;
				case 2:
					this.sharePopup()
				break;
			}
		},
		// 分享功能
		sharePopup(){
			this.$refs.popup.open()
		},
		//点击分享类型
		shareBtn(index){
			if(index === 0){



			}else if(index === 1){
				this.shareFriendsCircle()
			}
		},
		//分享到微信
		shareWx(){
			uni.share({
				provider: "weixin",
				scene: "WXSceneSession",
				type: 0,
				href: "http://192.168.2.84:8080/#/sub_school/pages/detail/index" + '?userId=1000000',
				title: "点击打开我分享的链接，带了参数userId",
				summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
				imageUrl: "https://uviewui.com/common/logo.png",
				success: function (res) {
					console.log("success:" + JSON.stringify(res));
				},
				fail: function (err) {
					console.log("fail:" + JSON.stringify(err));
				}
			});
		},
		//分享到朋友圈
		shareFriendsCircle(){
			uni.share({
			    provider: "weixin",
			    scene: "WXSenceTimeline",
			    type: 0,
			    href: "http://192.168.2.84:8080/#/sub_school/pages/detail/index" + '?userId=1000000',
			    title: "uni-app分享",
			    summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
			    imageUrl: "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
			    success: function (res) {
			        console.log("success:" + JSON.stringify(res));
			    },
			    fail: function (err) {
			        console.log("fail:" + JSON.stringify(err));
			    }
			});
		}
	},
	onShow(){
































	},
	watch:{
		list:{
			immediate: true,
			deep: true,
			handler(value) {   
			   this.arr = value
			}
		},
		//监听页面发生滚动就将浮动模块隐藏 同时兼容PC端上浏览时使用鼠标滚动而不是触摸
		scrollTop(newVal,oldVal){
			if(newVal !== oldVal){
				this.bgShow = false
				this.arr.map((item,i)=>{
					this.$set(item,'floatShow',false)
				})
			}
		}
	}
}
